import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { RpcNode } from '@cetus/types'
import { useEffect, useRef } from 'react'
import { usePreviousValue } from './usePreviousValue'

type UseRpcListenerProps = {
  onRpcChange: () => void
}

export function useRpcListener({ onRpcChange }: UseRpcListenerProps) {
  const { isInitLoading } = useClmmSDKStore()
  const { currentNode } = useRpcConfigStore()
  const preNode = usePreviousValue<RpcNode>(currentNode)
  const hasChangeRef = useRef<boolean>(false)

  useEffect(() => {
    if (preNode && currentNode && preNode.link !== currentNode?.link) {
      hasChangeRef.current = true
    }
  }, [preNode?.link, currentNode?.link, isInitLoading])

  useEffect(() => {
    if (hasChangeRef.current && !isInitLoading) {
      onRpcChange()
      hasChangeRef.current = false
    }
  }, [isInitLoading, hasChangeRef.current])
}
